.App {
  text-align: center;
  transition: all 0.3s ease;
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center; 
}

#timer, #player, .loading {
  font-size: calc(20px + 3vmin);
  color: #b9770e;
  text-shadow: 0px 1px 2px rgba(0, 0, 0, 0.8);
}

.error {
  font-size: calc(30px + 3vmin);
  color: red;
  text-shadow: 0px 1px 2px rgba(0, 0, 0, 0.8);
}

.btn {
  font-size: calc(10px + 1.5vmin);
  color: #fad7a0;
  background-color: #b9770e;
  box-shadow: 0px 1px 2px rgba(0, 0, 0, 0.8);
}

.btn:hover {
  color: #b9770e;
  background-color: #fad7a0;
  box-shadow: 0px 1px 2px rgba(0, 0, 0, 0.8);
}

.btn:focus {
  color: #b9770e;
  background-color: #fad7a0;
  box-shadow: 0px 0px 0px rgba(0, 0, 0, 0);
}

.info {
  display: flex;
  position: relative;
}

.chess-container {
  width: 80vw; 
  height: 80vw;
  background-color:  #fad7a0;  
  min-width: 350px;
  min-height: 350px;
  box-shadow: 0px 2px 4px rgba(0, 0, 0, 0.8);
  border-radius: 1%;
  display: flex;
  align-items: center;
  justify-content: center;
} 
  
.chess-board {
  width: 90%; 
  height: 90%;
  min-width: 300px;
  min-height: 300px;
  border: 1px  solid black ;
  font-size: 5vw;
}

td { 
  width: 12%;   
  height: 12%; 
  cursor: pointer;
}

.piece {
  cursor: pointer;
}

/* Selected chess piece */
.selected {
  -moz-box-shadow:    inset 0 0 7vw rgb( 142, 68, 173 , 0.7);
  -webkit-box-shadow: inset 0 0 7vw rgb( 142, 68, 173 , 0.7);
  box-shadow:         inset 0 0 7vw rgb( 142, 68, 173 , 0.7);
}

/* Available moves */
.highlighted {
  -moz-box-shadow:    inset 0 0 7vw rgb(171, 235, 198, 0.7);
  -webkit-box-shadow: inset 0 0 7vw rgb(171, 235, 198, 0.7);
  box-shadow:         inset 0 0 7vw rgb(171, 235, 198, 0.7);
}

/* Last move */
.highlighted-move {
  -moz-box-shadow:    inset 0 0 7vw rgb( 142, 68, 173 , 0.7);
  -webkit-box-shadow: inset 0 0 7vw rgb( 142, 68, 173 , 0.7);
  box-shadow:         inset 0 0 7vw rgb( 142, 68, 173 , 0.7);
}

/* Board black fields color */
.black-field {
  background-color:  #b9770e;

}

/* Board white fields color */
.white-field {
  background-color:  #fad7a0;

}

/* 100 * 16/9 = 177.778 */
@media (min-width: 100vh) {
  .chess-board {
    height: 90%;
    width: 90%;
    font-size: 5vh;
  }
  
  .chess-container {
  width: 80vh; 
  height: 80vh;
  }
}




